import React,{useState,useEffect} from 'react';
import InputFile from './../InputFile/InputFile';
import Notif from './../Notif/Notif'; 
import './FormAdd.css';
// import ReactTooltip from 'react-tooltip';

const defaultPicture = "https://image0.flaticon.com/icons/png/128/189/189334.png";

function FormAdd({type,onSubmit,load,className,onChange,imgsLength,autoFocus}){

    const [picture,setPicture] = useState("");
    const [errors,setErrors] = useState([]);
    const [name,setName] = useState("");
    const [refName] = useState( React.createRef() );

    useEffect( () => {

        if( refName.current instanceof Node ) {
            // here physical DOM is ok
            
            if( autoFocus ) {
                refName.current.focus();
            }
        }

    } ) ;

    return (
        <>
            <form
            className={`FormAdd ${className}`}
            onSubmit={e => {
                e.preventDefault();

                const data = {
                    name: name
                    ,picture: picture || defaultPicture
                    ,refName: refName
                    ,e: e
                } ;

                setName('') ;

                if( /album/i.test(type) )
                    delete data.picture;

                onSubmit( data );
            }}
        >

            <div className="field-wrap-pseudo">
                <input
                    ref={refName}
                    type="text"
                    value={name}
                    placeholder={`nom de l'${!/album/i.test(type) ? 'image':'album' }`}
                    autoComplete="off"
                    onChange={({target}) => setName( target.value ) }
                />
                <label 
                    htmlFor="name"
                    className={`${!!name.length ? 'active':'o-hide'}`}
                >
                    nom d'{!/album/i.test(type) ? 'image':'album' }
                </label>
            </div>

            {
                !/album/i.test(type) ?
                <InputFile
                    className="hide"
                    name="picture"
                    label={
                        <>
                            <Notif
                                tooltip="add-picture"
                                type="info"
                                text="ajout une image"
                                place="top"
                            />
                            <figure
                                data-tip="ajout une image"
                                data-for="add-picture"
                            >
                                <button
                                    type="button"
                                    onClick={e =>  e.target.parentNode.click()}
                                >
                                    <img
                                        src={defaultPicture}
                                        alt="ajout"
                                        width={64}
                                    />
                                </button>
                            </figure>
                        </>
                    }
                    autoRead
                    onChange={(file,blob,e) => {
                        if(
                            file.size <= 1e6 &&
                            /image/i.test(file.type)
                        ) {
                            setPicture( blob );

                            ( onChange => {
                                setName( '' );
                                onChange( file , blob , name , refName );
                            } )( onChange instanceof Function ? onChange : () => {} ) ;

                        } else {

                            const mssg = file.size > 1e6 ? 
                                "fichier trop volumineux":
                                "ce fichier n'est pas une image valide"
                            ;
                            
                            setErrors( [...errors , 
                                <Notif
                                    key={Date.now()} 
                                    type="error"
                                    text={mssg}
                                    onClose={({remove}) => remove()}
                                />
                            ] ) ;
                        }
                    }}
                />
                : null
            }

            <Notif
                tooltip="disabled"
                type="warn"
                text="chargement ..."
            />
            <button
                type='submit'
                disabled={!!load}
                className={!!load ? 'disabled':''}
                data-for={!!load ? 'disabled':''}
                data-tip="chargement ..."
            >
                {
                    load || (
                        <span>
                            ajout {!/album/i.test(type) ? (imgsLength+' images') : '\'album'}
                        </span>
                    )
                }
                
            </button>

        </form>
        {errors.map( error => error )}
      </>
    );
}

export default FormAdd;